

































#form-container{
    width: 50dvw;
    min-width: 200px;
    margin-left: 25dvw;
    margin-top:10px;
    border:1px solid lavender;
    background-color:ghostwhite;
    padding-top: 20px;
}
.section{
    margin-left: 20px;
    border-bottom: 1px solid lavender;
    margin-right: 20px;
    padding-bottom: 20px;
}
.section-name{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:mediumslateblue;
    font-size: 17px;
}
.row{
    margin-left: 10px;
    margin-bottom: 5px;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.label{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 15px;
    width: 10dvw;
    min-width: 150px;
}
.input-text{
    border:1px solid lavender;
    border-radius: 5px;
    height:20px;
    font-size: 16px;
    color:dimgrey;
}
.input-select{
    border:1px solid lavender;
    border-radius: 5px;
    color:dimgray;
}
#button{
    margin-left:40%;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid lavender;
    border-radius: 5px;
    background-color:lightblue;
    height: 30px;
    font-size: 14px;
}
.wrapper{
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: end;
    width: 100%;
}
.errorMessage{
    color:red;
    margin-top:0px;
    padding-top:0px;
    font-size: 13px;
}

#container{
    width: 50dvw;
    min-width: 200px;
    margin-left: 25dvw;
    margin-top:10px;
    border:1px solid lavender;
    background-color:ghostwhite;
    padding-top: 20px;
}
#message{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:mediumslateblue;
    font-size: 17px;
    margin-left:20px;
}
